@mixin cent {
    display: flex;
    justify-content: center;
    align-items: center;
}

.Interior_1_container {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(2, auto);
    padding-left: 10vw;
    padding-right: 10vw;
    padding-top: 5em;

    .project_display {
        @include cent();
        flex-wrap: wrap;
        display: grid;
        grid-template-rows: repeat(10, auto);
        grid-template-columns: 1fr;
        padding-bottom: 2em;

        a {
            margin-bottom: 2em;
            font-family: 'Montserrat', sans-serif;
            font-size: .8em;
            font-weight: 800;
            width: 12em;
            height: 2em;
            @include cent();
            border: .1em solid #000;
            border-radius: .3em;
            text-decoration: none;
            color: var(--colorX);

            &:hover {
                transform: scale(1.02);
            }

            ion-icon {
                font-size: 1.2em;
                // bottom: -.1em;
            }

        }

        .the_video {
            width: 60vw;
            height: 45vw;
            margin-bottom: 3em;
        }

        .the_video_2 {
            height: 34vw;
        }

        .project_title {
            font-family: 'Montserrat', sans-serif;
            font-size: 2em;
            font-weight: 800;
            margin-bottom: .2em;
        }

        .project_description {
            margin-bottom: 2em;
            font-family: 'Montserrat', sans-serif;
            font-size: 1.2em;
            font-weight: 400;
            cursor: pointer;
        }

        .TheImageContainer {
            width: 60vw;
            height: auto;
            margin-bottom: 3em;
        }

        p {
            width: 60vw;
            margin-bottom: 2em;
            font-family: 'Montserrat', sans-serif;
            font-size: .8em;
            font-weight: 700;
        }
    }
}

//!########################################

@media all and (max-width: 1000px) {
    .Interior_1_container {
        padding-left: 5vw;
        padding-right: 0vw;

        .project_display {
            .project_title {
                font-size: 1.5em;
            }

            .project_description {
                font-size: .7em;
            }

            .the_video {
                width: 90vw;
                height: 65vw;
                margin-bottom: 3em;
            }

            .TheImageContainer {
                width: 90vw;
                margin-bottom: 1em;
            }

            p {
                width: 80vw;
            }
        }
    }
}